  <div class="background-dfg"></div>
  <div class="background-process-tree"><ProcessTree></ProcessTree></div>
  <div>
  <button class="back" @click="stepBackward">Back</button>
  </div>
  <div>
     <button class="next" @click="stepForward" :disabled="currentStep >= maxSteps">Next</button>
  </div>
  <div>
    <button class="file-upload" @click="handleClick">Select File</button>
    <input type="file" ref="fileInput" @change="handleChange" style="display: none;"/>
  </div>
  
  <img class="uni-mannheim" src= @/assets/uni_mannheim.png/>
  <div>
    <button class="popup-button" @click="showPopup = true">Legende</button>
    <div v-if="showPopup" class="popup-frame">
      <div class="popup-input">
        <h2>Legende</h2>
        <p>....</p>
        <button @click="showPopup = false">Close</button>
      </div>
    </div>
  </div>
</template>

<script>
import ProcessTree from '@/components/ProcessTree.vue' 


export default {
  components:{
    ProcessTree
  },
  methods:{
    handleClick() {
      this.$refs.fileInput.click();//FOR FILE UPLOAD
    },
    handleChange(event) {
      const file = event.target.files[0];
      console.log(file);
    },
    stepForward() {//FOR STEP FORWARD BUTTON
      if(this.currentStep < this.maxSteps)
        this.currentStep++;
    },
    stepBackward() {//FOR STEP BACKWARD BUTTON
      if(this.currentStep > 1) {
        this.currentStep--;
      }  
    },
  data(){
    return {
      currentStep: 1, // Nicht sicher ob 1 oder 0
      maxSteps: 4,//Hier muss noch eine varaibale für die gesamt anzahl der schritte übergeben werden
     
    }
  }
  },
  data(){
    return{showPopup: false,
      title: "description", //if we want to use the pop up for the description
      message: "Sample Text"}
  }
}
</script>
