<template>
   <div class="container">
      <vue-tree
        style="width: 700px; height: 721px"
        :dataset="PTNode"
        :config="treeConfig"
      >
        <template v-slot:node="{ node, collapsed }">
          <div
            class="PTNode"
            :style="{ border: collapsed ? '2px solid grey' : '' }"
          >
            <img
              :src= "node.avatar"
              style="border-radius: 4px;height:55px; width:55px"
            />
            <span style="padding: 4px 0; font-weight: bold;"
              >{{ node.value }}</span
            >
          </div>
        </template>
      </vue-tree>
      <div style="display: flex;">
      <v-btn @click="controlScale('ZoomIn')">+</v-btn>
      <v-btn @click="controlScale('ZoomOut')">-</v-btn>
      <v-btn @click="controlScale('restoreScale')">1:1</v-btn>
    </div>
  </div>
  
        
  </template>
  
  
  <script>
  
  import VueTree from "@ssthouse/vue3-tree-chart";
  import "@ssthouse/vue3-tree-chart/dist/vue3-tree-chart.css";
  
  export default {
    name: "ProcessTree",
    components: { VueTree },
    methods:{
        controlScale(command) {
        switch(command){
            case 'ZoomIn':
            this.$refs.tree.zoomIn()
            break
            case 'ZoomOut':
            this.$refs.tree.zoomOut()
            break
            case 'restoreScale':
            this.$refs.tree.restoreScale()
            break
        }}
     },
    data() {
      return {
        PTNode: { //Pass TreeData
          value: 800,
          children: [
            {
            
              avatar: require('@/assets/XOR.png'),
              value: 'A',
              children: [
                {
                  value: 100,
                  avatar: require('@/assets/AND.png'),
                },
                {
                  value: 300,
                },
                {
                  value: 200,
                },
              ],
            },
            {
              value: 200,
              children: [
                {
                  value: 230,
                },
              ],
            },
            {
              value: 300,
            },
          ],
        },
        treeConfig: { nodeWidth: 120, nodeHeight: 80, levelHeight: 200 },
      };
    },
  };
  </script>
  
  <style scoped lang="less">
  .ProcessTree-layout {
    width: 700px;
    height: 721px;
  }
  .PTNode {
  width: 80px;
  padding: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  background-color: #516D82;
  border-radius: 4px;
}
  </style>